const drilldownMenu = document.querySelector( '.drilldown' );
const submenu = drilldownMenu.querySelectorAll( 'ul.nested' );

submenu.forEach(expand);

function expand(item) {
	let link = item.previousElementSibling; // find <a> tag next to <ul>
	link.setAttribute('href', "javascript:;");
	let backLink = document.createElement("li");
	backLink.innerHTML = '<a href="javascript:;">Back</a>';

	link.addEventListener( 'click', function() {
		item.classList.add('is-visible')
	});

	backLink.addEventListener( 'click', function() {
		item.closest('ul').classList.remove('is-visible')
	})

	item.insertBefore(backLink, item.firstChild); // inject HTML back button
}
