  
function video() {
  var videoWrapper = document.querySelector('.video-embed');
  var video = document.querySelector('.video-embed iframe');

  if (video !== null) {
  var w = videoWrapper.offsetWidth;
  var h = videoWrapper.offsetHeight;

 
  var width = w;
  var height = w / (16/9);

  if (height < h) {
    height = h;
    width = h * (16/9);
  }

  // // Round
  height = Math.ceil(height);
  width = Math.ceil(width);

  // // Adjust
  var top = Math.round(h / 2 - height / 2);
  var left = Math.round(w / 2 - width / 2);



  video.style.position = "absolute";
  video.style.left = left + "px";
  video.style.top = top + "px";
  video.width = "100%";
  video.height = "100%";
  video.style.width = width + "px";
  video.style.height = height + "px";
  }
}

video();

window.addEventListener('load', (event) => {
  video();
});

window.onresize = function (event) {
  video();
};